<template>
  <div id="app" class="mx-md-5 mx-2">
    <div class="container mt-5">
      <div class="pb-2 mb-3 border-bottom">
        <h4 class="text-left">Data Integration Demo</h4>
      </div>
      <div class="row">
        <div v-for="source in dataSource" :key="source.name" class="col-12 col-md-6 col-lg-4 my-2">
          <DataSource :data="source" :modalName="source.modalName" />
        </div>
      </div>
      <div class="d-flex flex-column flex-md-row mt-5">
        <div class="d-flex flex-column align-items-start">
          <h4 class="mb-0">Integrate Data</h4>
          <small
            class="text-muted text-left"
          >Click the button below to integrate data from the data sources</small>
          <button
            style="width: 170px;"
            @click="integrateData"
            :disabled="isIntegratingData"
            class="btn btn-primary btn-lg my-3"
          >
            <span v-if="!isIntegratingData">Integrate Data</span>
            <div v-else class="spinner">
              <div class="bounce1"></div>
              <div class="bounce2"></div>
              <div class="bounce3"></div>
            </div>
          </button>
        </div>
        <div v-if="doneIntegrating" class="ml-0 ml-md-5 mt-3" style="flex-basis: 60%">
          <DataSource :data="integratedData" :modalName="'integrated'" :isGenerated="true" />
        </div>
      </div>
      <div class="modal fade" id="integratingModal" tabindex="-1" role="dialog">
        <div class="modal-dialog modal-dialog-centered" role="document">
          <div class="modal-content">
            <div class="modal-body">
              <div class="h-100 d-flex flex-column justify-content-center align-items-center my-5 py-5">
                <div class="sk-chase" v-if="!doneIntegrating">
                  <div class="sk-chase-dot"></div>
                  <div class="sk-chase-dot"></div>
                  <div class="sk-chase-dot"></div>
                  <div class="sk-chase-dot"></div>
                  <div class="sk-chase-dot"></div>
                  <div class="sk-chase-dot"></div>
                </div>
                <div v-else>
                  <span>Done</span>
                </div>
                <h5 v-if="!doneIntegrating" class="mt-3">{{currentProcess}}</h5>
                <div v-else class="d-flex flex-column justify-content-center align-items-center">
                  <h5>Integrated Data Generated Successfully</h5>
                  <button @click="closeModal" class="btn btn-outline-primary align-self-center">Ok</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import DataSource from "./components/DataSource.vue";
import dataSource from "./data/data-sources";
import $ from 'jquery';

export default {
  name: "App",
  components: {
    DataSource
  },
  data() {
    return {
      dataSource: dataSource.dataSources,
      isIntegratingData: false,
      dataIntegratingProcesses: [
        "Normalizing Data..",
        "Removing Duplicates...",
        "Integrity Violation Checks..."
      ],
      currentProcessIndex: 0,
      currentProcess: "",
      integratedData: dataSource.integratedData,
      doneIntegrating: false
    };
  },
  methods: {
    integrateData() {
      this.isIntegratingData = true;
      $('#integratingModal').modal('show');
      this.currentProcess = this.dataIntegratingProcesses[
        this.currentProcessIndex
      ];
      let interval = setInterval(() => {
        if (this.currentProcessIndex != 2) {
          this.currentProcess = this.dataIntegratingProcesses[
            ++this.currentProcessIndex
          ];
        } else {
          clearInterval(interval);
          this.doneIntegrating = true;
          this.isIntegratingData = false;
          this.currentProcessIndex = 0;
        }
      }, 3000);
    },

    closeModal(){
      $('#integratingModal').modal('hide');
    }
  }
};
</script>

