<template>
  <div class="d-flex border shadow-sm py-3">
    <img height="150" src="../assets/db.png" />
    <div class="d-flex flex-column align-items-start my-3 ml-3">
      <small class="text-muted">Data Source</small>
      <h5 class="align-self-start">{{data.name}}</h5>

      <div class="d-flex">
        <button @click="showModal" class="btn btn-outline-primary btn-sm mt-3">View Data</button>
        <button v-show="!isGenerated" class="btn btn-outline-primary btn-sm mt-3 ml-2">Upload</button>
      </div>
    </div>

    <div class="modal fade" :id="modalName" tabindex="-1" role="dialog">
      <div class="modal-dialog modal-xl" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">{{data.name}}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="table-responsive">
              <table class="table table-hover">
                <thead>
                  <tr>
                    <th v-for="dataPointKey in Object.keys(data.data)" :key="dataPointKey" scope="col">{{parseDataPointKey(dataPointKey)}}</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td v-for="dataPoint in data.data" :key="dataPoint">{{dataPoint}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">Ok</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  name: "DataSource",
  props: {
    data: Object,
    modalName: String,
    isGenerated: Boolean
  },
  methods: {
    showModal() {
      $(`#${this.modalName}`).modal("show");
    },
    parseDataPointKey(data){
      return data.toString().replace(/_/g, ' ');
    }
  }
};
</script>